body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.modal-content {
  background-color: black;
  border-radius: 0px;
  border-color: white;
}

.modal-header .modal-title {
  color: white;
}

.modal-body .form-group .form-control {
  background-color: black;
  color: white;
  border-radius: 0px;
  border-color: white;
  -webkit-appearance: none;
}

.modal-footer .mdl-btn {
  color: white;
  background-color: black;
  font-weight: bold;
}

.modal-footer .mdl-btn-close:active {
  background-color: gray;
}

.modal-footer .mdl-btn-primary {
  background-color: blue;
}

.modal-footer .mdl-btn-primary:active {
  background: steelblue;
}

.has-error .form-control:focus {
  border-color: #843534 !important;
}

.has-error .form-control {
  border-color: #a94442 !important;
}

.has-success .form-control {
  border-color: #3c763d !important;
}

.has-success .form-control:focus {
  border-color: #2b542c !important;
}

.btn, .dropdown-menu {
  border-radius: 0px;
}

.app-container {
    display: flex;
    flex-direction: column;
}

.app-header-container {
    flex: 1 1;
    position: fixed;
    width: 100%;
    z-index: 999;
    background-color: black;
}

.app-header-container-color-top {
    opacity: 1;
}

.app-header-container-color-scroll {
    opacity: 0.85;
}
.header-banner-container {
    width: 100%;
    display: flex;
}

.header-content-alignment {
    display: flex;
    flex: 1 1;
    height: 100px;
    align-items: center;
}

.header-logo {
    color: white;
    flex: 33% 1;
    text-align: left;
    margin-left: 25px;
}

.header-action-items {
    flex: 20% 1;
    text-align: center;
}

.header-navbar {
    flex: 0% 1;
    list-style: none;
}

.header-action-user {
    flex: 20% 1;
    text-align: center;
    color: white;
    font-size: x-large;
}
.carousel-item-inner-container {
    background-color: white;
    display: flex;
    justify-content: center;
}
.navbar-container .caret {
    display: none;
}

.navbar-container .dropdown-toggle {
    text-decoration: none;
    color: white;
    font-size: 34px;
    padding: 0px 10px 5px 10px;
}

.navbar-container .dropdown-menu {
    border-color: gray;
    background-color: black;
}

.navbar-container .dropdown-menu a {
    color: white;
    font-weight: bold;
}

.navbar-container .dropdown-menu a:hover {
    background-color: gray;
}

.navbar-container .dropdown-toggle:hover {
    background-color: gray;
}
.content-text {
    margin: 0% 25% 0% 25%;
}

.content-text-image-right {
    display: flex;
}
.footer-main {
    width: 100%;
    height: 250px;
    background-color: black;
}

.footer-logo > img {
    width: 100px;
    height: 100px;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    margin-left: 25px;
    margin-right: 20px;
}

.footer-logo > span {
    font-size: x-large;
    font-weight: bold;
    color: white;
}

.card-main{
    margin-top: 8%;
    width: 30%;
    display: flex;
    justify-content: center;
}

.card-main .carousel-indicators {
    display: none;
}

.card-main .carousel-control {
    color: gold;
    background-image: none;
    opacity: 1;
}

.card-main .carousel-control.right {
    right: -65px;
}

.card-main .carousel-control.left {
    left: -65px;
}

.card-dimension {
    width: 370px;
    height: 230px;
    border: 1px solid;
    display: flex;
    flex-direction: column;
}

.card-header {
    flex: 0.25 1;
    border-bottom: 3px solid gold;
    background-color: black;
    display: flex;
}

.card-header span {
    text-align: left;
    color: white;
    font-size: x-large;
    margin-top: 20px;
    margin-left: 20px;
    font-family: fantasy;
}

.card-body {
    flex: 0.50 1;
    background-color: black;
    display: flex;
    align-items: center;    
}

.card-body span {
    color: white;
    flex: 1 1;
    font-family: cursive;
    font-style: italic;
    font-size: 40px;
    font-weight: bold;
}

.card-footer {
    flex: 0.25 1;
    border-top: 3px solid gold;
    background-color: black;
    display: flex;   
    text-align: left;
}

.card-footer span {
    color: white;
    flex: 1 1;
    align-self: flex-end;
}


.thumbnail-border {
    border: 1px solid gray;
    border-radius: 5px;
    height: 320px;
    width: 380px;
    margin: 8% 5% 8% 8%;
    display: flex;
    flex-direction: column;
}

.thumbnail-inner-card {
    border: 1px solid;
    width: 370px;
    height: 230px;
    margin-top: 4px;
    margin-left: 4px;
    display: flex;
    flex-direction: column;
}

.thumbnail-inner-card-header {
    flex: 0.25 1;
    border-bottom: 3px solid gold;
    background-color: black;
    display: flex;
}

.thumbnail-inner-card-header span {
    text-align: left;
    color: white;
    font-size: x-large;
    margin-top: 20px;
    margin-left: 20px;
    font-family: fantasy;
}

.thumbnail-inner-card-body {
    flex: 0.50 1;
    background-color: black;
    display: flex;
    align-items: center;
    padding-left: 10px;
    padding-right: 10px;
}

.thumbnail-inner-card-footer {
    flex: 0.25 1;
    border-top: 3px solid gold;
    background-color: black;
    display: flex;   
    text-align: left;
}

.thumbnail-inner-card-body span {
    color: white;
    flex: 1 1;
    font-family: cursive;
    font-style: italic;
    font-size: 40px;
    font-weight: bold;
}
.thumbnail-inner-card-footer span {
    color: white;
    flex: 1 1;
    align-self: flex-end;
}

.thumbnail-inner-card-option{
    margin: 5%;
}
.home-container {
    display: flex;
    flex-direction: column;
}

.home-carousel-container {
    flex: 1 1;
    margin-top: 100px;
}

.home-license-cards {
    flex: 1 1;
    text-align: center;
    background-color: white;
    display: flex;
    justify-content: center;
}

.home-content {
    flex: 1 1;
    text-align: center;
    padding-bottom: 8%;
    background-color: white;
}

.home-content > div {
    margin-top: 8%;
}

.home-book-button button {
    border: 1px solid black;
    background-color: black;
    color: white;
}

.home-book-button button:hover {
    background-color: gray;
}
.react-datepicker-wrapper .react-datepicker__input-container input {
    background-color: black;
    color: white;
    border: 1px solid white;
    padding: 5px 13px;
}

.react-datepicker-popper .react-datepicker {
    font-size: small;
    background-color: #000;
    color: #fff;
    border: 1px solid #ffffff;
    border-radius: 0px;
}

.react-datepicker-popper .react-datepicker .react-datepicker__triangle {
    border-top-color: #000;
}

.react-datepicker-popper .react-datepicker .react-datepicker__navigation--previous.react-datepicker__navigation--next {
    border-right-color: #fff;
    border-left-color: #fff;
}

.react-datepicker-popper .react-datepicker .react-datepicker__header {
    background-color: #000000;
    border-bottom: 1px solid #ffffff;
}

.react-datepicker-popper .react-datepicker .react-datepicker__header .react-datepicker__header__dropdown .react-datepicker__month-dropdown {
    background-color: black;
    border: 1px solid gray;
    border-radius: 0px;
}

.react-datepicker-popper .react-datepicker .react-datepicker__header .react-datepicker__header__dropdown .react-datepicker__month-dropdown .react-datepicker__month-option {
    color: white;
}

.react-datepicker-popper .react-datepicker .react-datepicker__header .react-datepicker__header__dropdown .react-datepicker__month-dropdown .react-datepicker__month-option .react-datepicker__month-option--selected{
    position: static;
}

.react-datepicker-popper .react-datepicker .react-datepicker__header .react-datepicker__header__dropdown .react-datepicker__month-dropdown .react-datepicker__month-option:hover {
    background-color: gray;
}

.react-datepicker-popper .react-datepicker .react-datepicker__header .react-datepicker__current-month {
    color: #fff;
    font-size: 12px;
}

.react-datepicker-popper .react-datepicker .react-datepicker__header .react-datepicker__header__dropdown .react-datepicker__year-dropdown {
    background-color: black;
    border: 1px solid gray;
    border-radius: 0px;
}

.react-datepicker-popper .react-datepicker .react-datepicker__header .react-datepicker__header__dropdown .react-datepicker__year-dropdown .react-datepicker__year-option {
    color: white;
}

.react-datepicker-popper .react-datepicker .react-datepicker__header .react-datepicker__header__dropdown .react-datepicker__year-dropdown .react-datepicker__year-option .react-datepicker__year-option--selected{
    position: static;
}

.react-datepicker-popper .react-datepicker .react-datepicker__header .react-datepicker__header__dropdown .react-datepicker__year-dropdown .react-datepicker__year-option:hover {
    background-color: gray;
}

.react-datepicker-popper .react-datepicker .react-datepicker__header .react-datepicker__current-year {
    color: #fff;
    font-size: 12px;
}

.react-datepicker-popper .react-datepicker .react-datepicker__month .react-datepicker__week .react-datepicker__day {
    color: white;
}

.react-datepicker-popper .react-datepicker .react-datepicker__month .react-datepicker__week .react-datepicker__day.react-datepicker__day--selected {
    border-radius: 0px;
    background-color: blue;
}

.react-datepicker-popper .react-datepicker .react-datepicker__month .react-datepicker__week .react-datepicker__day:hover {
    background-color: lightskyblue;
    border-radius: 0px;
}

.register-button-container{
    display: flex;
    justify-content: flex-end;
    border-top: 1px solid white;
    padding-top: 15px;
    flex-direction: column;
    align-items: flex-end;
}

.register-button-elements {
    background-color: black;
    color: white;
    font-weight: bold;
}

.register-button-close {
    margin-right: 5px;
}

.register-button-submit {
    background-color: blue;
    color: white;
}

.register-submit-error-msg {
    color: red;
    font-weight: bold;
}

.register-submit-error-hidden {
    display: none;
}

.register-submit-error-visible {
    display: block;
}
.license-container {
    margin-top: 100px;
    background-color: white;
    flex: 1 1;
}

.license-grid-container {
    display: grid;
    grid-gap: 50px;
    grid-template-columns: auto auto auto;
}

.license-heading {
    margin-left: 2.5%;
    padding-top: 5px;
    font-style: oblique;
}

.license-book-btn {
    display: flex;
    justify-content: center;
    padding-bottom: 35px;
}

.license-book-btn button {
    background-color: black;
    color: white;
    font-weight: bold;
    padding: 15px 25px 15px 25px;
}
.destination-container {
    margin-top: 100px;
    background-color: white;
    display: flex;
    flex-direction: column;
}

.destination-map-container {
    margin: 5%;
}

#map {
    width: 100%;
    height: 400px;
}

#pac-input{
    width: 25%;
    margin-bottom: 2%;
    border: 1px solid black;
    height: 40px;
}
.destination-next-button {
    display: flex;
    justify-content: center;
    margin-top: 5%;
}
.destination-next-button button {
    background-color: black;
    color: white;
    font-weight: bold;
    padding: 15px 25px 15px 25px;
}

.payment-container {
    margin-top: 100px;
    background-color: white;
    display: flex;
    flex-direction: column;
}

.payment-heading {
    flex: 1 1;
    display: flex;
    margin-top: 100px;
    margin-left: 10%;
    margin-bottom: 5%;
}

.payment-heading-title{
    font-size: xx-large;
    text-decoration: underline;
    margin-left: 3%;
}

.payment-heading-logo{
    width: 100px;
    height: 100px;
    margin-top: -37px;
}

.payment-cards {
    margin-left: 10%;
    margin-bottom: 5%;
    display: grid;
    grid-gap: 50px;
    grid-template-columns: auto auto auto;
}

.payment-continue {
    display: flex;
    justify-content: center;
    margin-bottom: 5%;
}

.payment-continue button {
    background-color: black;
    color: white;
    font-weight: bold;
    padding: 15px 25px 15px 25px;
}
.confirm-container {
    margin-top: 100px;
    display: flex;
    background-color: white;
}

.confirm-content {
    width: 100%;
    height: 400px;
}

.confirm-content {
    display: flex;
    justify-content: center;
    margin-top: 5%;
    flex: 1 1;
}

.confirm-hide-loader {
    display: none;
}

.confirm-loader-booking-text {
    margin-top: 25%;
    margin-left: 25%;
}
